import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import { withStyles } from '@material-ui/core/styles';

const styles = {
  row: {
    display: 'flex',
    alignItems: 'center',
    marginTop: 10,
    marginBottom: 10,
  },
  flags: {
    marginLeft: 30,
    minWidth: 180
  }
};

export default withStyles(styles)(function ({ value, className, classes, onRemove, onChange, onCheck }) {
  const { name, flags, pattern, item } = value;

  return (
    <div className={className}>
      <div className={classes.row}>
        <TextField
          fullWidth
          label="Name"
          variant="outlined"
          value={name}
          onChange={onChange('name')}
        />
        <FormGroup row className={classes.flags}>
          {Object.keys(flags).map(key => (
            <FormControlLabel control={
              <Checkbox
                value={key}
                checked={flags[key]}
                onChange={onCheck(key)} />
            } label={key} />
          ))}
        </FormGroup>
        <Button variant="outlined" color="secondary" onClick={onRemove}>
          Remove
        </Button>
      </div>
      <div className={classes.row}>
        <TextField
          fullWidth
          variant="outlined"
          label="Pattern"
          value={pattern}
          onChange={onChange('pattern')}
        />
      </div>
      <div className={classes.row}>
        <TextField
          fullWidth
          variant="outlined"
          label="Item"
          value={item}
          onChange={onChange('item')}
        />
      </div>
    </div>
  );
});